@charset "utf-8";
@import "lib/reset.min.css";
@import "_vars.sass";

.is-sp {
  display: none; }

body {
  color: $black;
  background: $white;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  font-size: 16px;
  line-height: 2.0;
  @include ff_TPSky_R; }
a {
  color: $cyan; }

li {
  list-style: none; }

header {
  position: relative;
  padding: 40px;
  h1 {
    img {
      width: 180px; } }
  nav {
    position: absolute;
    top: 40px;
    right: 40px;
    li {
      line-height: 49px;
      display: inline-block;
      margin-left: 20px;
      @include ff_TPSky_B;
      span {
        opacity: 0.3; }
      a {
        color: $pink; } } } }

footer {
  background: $pink;
  position: relative;
  padding: 40px;
  p.address {
    display: inline-block;
    color: $white;
    font-size: 12px;
    line-height: 1.5;
    width: 270px;
    white-space: nowrap;
    span {
      @include ff_TPSky_B; } }
  p.logo {
      position: absolute;
      top: 40px;
      right: 40px;
      img {
        width: 180px; } } }

body.index {
  width: 100%;
  height: 100%;
  .kv {
    position: relative;
    background: url("/assets/img/top/kv.jpg") no-repeat center;
    background-size: cover;
    .inner {
      background: rgba(0,0,0,0.5) url("/assets/img/top/bg.png") no-repeat top right;
      background-size: contain;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      .catch {
        @include ff_TPSky_L;
        font-size: 36px;
        padding-top: 22+40px;
        padding-left: 40px;
        color: $white; }
      .copy {
        line-height: 1.0;
        font-size: 10px;
        color: $white;
        position: absolute;
        bottom: 40px;
        right: 40px; } } } }


body.about {
  background: $pink;
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 900px 1280px; }

  #container {
    width: 970px;
    margin: 0 auto;
    margin-top: 100px;

    h2 {
      text-align: center;
      line-height: 1.0;
      font-size: 48px;
      @include ff_TPSky_L;
      margin-bottom: 80px;
      position: relative;
      &:before {
        position: absolute;
        content: '';
        top: -50px;
        left: 50%;
        width: 40px;
        height: 1px;
        background: $pink;
        margin-left: -20px; }
      span {
        @include ff_TPSky_R;
        display: block;
        font-size: 12px;
        letter-spacing: 0.2em;
        margin-bottom: 30px; } }

    .vision {
      margin-bottom: 200px;
      h2 {
        img {
          width: 690px; }
        &:before {
          display: none; }
        font-size: 64px; }
      .culum-wraper {
        .culum {
          &:first-child {
            margin-right: 40px;
            float: left;
            width: 465px; }
          p {
            margin-bottom: 2em;
            &:last-child {
              margin-bottom: 0em; } } } } }

    .lineup {
      margin-bottom: 200px;
      h2 {
        img {
          width: 192px; } }
      ul {
        padding-left: 2em;
        li {
          @include ff_TPSky_L;
          font-size: 36px;
          list-style: disc;
          margin-top: -16px;
          margin-bottom: 50px; } } }

    .access {
      margin-bottom: 150px;
      h2 {
        img {
          width: 192px; } }
      .culum-wraper {
        .culum {
          float: left;
          width: 465px;
          p {
            margin-top: -8px;
            margin-bottom: 50px;
            span {
              @include ff_TPSky_B; } }
          &.map {
            width: 505px;
            height: 505px;
            background: $gray; } } } } } }

body.member {
  background: $pink;
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 900px 1280px; }

  #container {
    margin-top: 100px;
    .member {
        margin: 0 auto;
        vertical-align: top;
        margin-bottom: 200px;
        margin-top: 50px;
        &:last-child {
          margin-bottom: 150px; }
        &:first-child {
          margin-top: 0px;
          .name {
            &:before {
              display: none; } } }
        .img {
          float: left;
          width: calc(((100vw - 775px) / 2) + 195px);
          padding-left: calc((100vw - 775px) / 2);
          box-sizing: border-box;
          margin-right: 40px;
          background: rgba(255,0,110,0.2);
          img {
            width: 100%; } }
        .name {
          position: relative;
          float: left;
          width: 190px;
          margin-right: 40px;
          h2 {
            font-size: 36px;
            line-height: 1.0;
            @include ff_TPSky_L; }
          .en {
            font-size: 12px;
            line-height: 1.0;
            letter-spacing: 0.2em;
            margin-top: 20px; }
          .position {
            font-size: 12px;
            margin-top: 50px; }
          &:before {
            position: absolute;
            content: '';
            top: -50px;
            left: 0px;
            width: 40px;
            height: 1px;
            background: $pink; } }
        .desc {
          margin-top: -8px;
          float: left;
          width: 505px; } } } }

body.contact {
  background: $pink;
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 900px 1280px; }

  #container {
    width: 970px;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 100px;
    .lead {
      margin-bottom: 150px;
      font-size: 36px;
      @include ff_TPSky_EL; }
    .form {
      position: relative;
      padding-top: 50px;
      &:before {
        position: absolute;
        content: '';
        top: 0px;
        left: 0px;
        width: 40px;
        height: 1px;
        background: $pink; }
      div {
        margin-bottom: 50px;
        p {
          font-size: 12px;
          letter-spacing: 0.2em;
          margin-bottom: 20px;
          &.validation {
            opacity: 0.0;
            margin-top: -20px;
            text-align: center;
            color: $pink; }
          .req {
            color: $pink;
            @include ff_TPSky_B; } } }
      input[type="text"] {
        color: $black;
        font-size: 20px;
        width: 100%;
        padding: 20px;
        box-sizing: border-box;
        outline: none;
        border: 1px solid rgba(0,0,0,0.3);
        border-radius: 4px;
        &:focus {
          border: 1px solid $cyan; }
        &.error {
          border: 1px solid $pink; }
        &:placeholder-shown {
          color: #E0E0E0; }
        &::-webkit-input-placeholder {
          color: #E0E0E0; } }


      input[type="radio"] {
        display: none; }

      input[type="radio"]:checked + label::after {
        position: absolute;
        content: '';
        top: 50%;
        left: 7px;
        width: 18px;
        height: 18px;
        margin-top: -8px;
        background: #3498db;
        border-radius: 100%; }

      label {
        color: $black;
        line-height: 1.0;
        margin-bottom: 15px;
        cursor: pointer;
        position: relative;
        display: block;
        padding: 7px 7px 7px 50px;
        em {
          font-style: normal; }
        span {
          @include ff_TPSky_B; }
        &:before {
          position: absolute;
          content: '';
          top: 50%;
          left: 0;
          width: 30px;
          height: 30px;
          margin-top: -15px;
          background: #fff;
          border: 1px solid #ccc;
          border-radius: 100%; } }

      textarea {
        color: $black;
        font-size: 20px;
        width: 100%;
        height: 180px;
        padding: 20px;
        box-sizing: border-box;
        outline: none;
        border: 1px solid rgba(0,0,0,0.3);
        border-radius: 4px;
        &:focus {
          border: 1px solid $cyan; }
        &.error {
          border: 1px solid $pink; }
        &:placeholder-shown {
          color: #E0E0E0; }
        &::-webkit-input-placeholder {
          color: #E0E0E0; } }

      a.submit {
        display: block;
        text-align: center;
        text-decoration: none;
        cursor: pointer;
        color: $white;
        font-size: 16px;
        width: 100%;
        box-sizing: border-box;
        padding: 30px;
        border-radius: 4px;
        background: $cyan; } } } }

body.topics {
  background: $pink;
  .wraper {
    background: $white url("/assets/img/common/bg.png") no-repeat top right;
    background-size: 900px 1280px; }
  #container {
    width: 970px;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 100px;
    .nav {
        margin-bottom: 200px;
        a {}
        .old {
          float: right; }
        .new {
          float: left; } }
    .posts {
      .post {
        position: relative;
        margin-top: -16px;
        margin-bottom: 100px;
        &:after {
          position: absolute;
          content: '';
          bottom: -150px;
          left: 195px;
          width: 40px;
          height: 1px;
          background: $pink; }
        &:last-child {
          &:after {
            display: none; } }
        .date {
          position: absolute;
          top: 16px;
          left: 0px;
          @include ff_TPSky_B; }
        .post-body {
          width: 775px;
          margin-left: 195px;
          h2 {
            font-size: 36px;
            @include ff_TPSky_L;
            margin-bottom: 62px; }
          .text {
            margin-bottom: -8px;
            margin-top: -8px; }
          p {
            margin-top: -8px;
            margin-bottom: 50px; }
          figure {
            margin-left: -310px;
            margin-right: -116px;
            background: rgba(0,0,0,0.05);
            padding: 30px;
            text-align: center;
            margin-bottom: 50px;
            img {
              max-width: 100%; } }
          .foot {
            margin-top: 80px;
            line-height: 1.0;
            position: relative;
            .links {
              position: absolute;
              top: 0px;
              right: 0px;
              li {
                display: inline-block;
                margin-left: 20px; } } } } } } } }
