@charset "utf-8";

//color
$black: #646469;
$white: #fff;
$pink: #FF006E;
$cyan: #009DFF;
$gray: #bdbdbd;

//font-weight
$fw-regular: 400;
$fw-bold: 700;

//breakpoint
$breakpoint: 768px;

//easing
$easeInSine:      cubic-bezier(0.47, 0, 0.745, 0.715);
$easeOutSine:     cubic-bezier(0.39, 0.575, 0.565, 1);
$easeInOutSine:   cubic-bezier(0.445, 0.05, 0.55, 0.95);
$easeInQuad:      cubic-bezier(0.55, 0.085, 0.68, 0.53);
$easeOutQuad:     cubic-bezier(0.25, 0.46, 0.45, 0.94);
$easeInOutQuad:   cubic-bezier(0.455, 0.03, 0.515, 0.955);
$easeInCubic:     cubic-bezier(0.55, 0.055, 0.675, 0.19);
$easeOutCubic:    cubic-bezier(0.215, 0.61, 0.355, 1);
$easeInOutCubic:  cubic-bezier(0.645, 0.045, 0.355, 1);
$easeInQuart:     cubic-bezier(0.895, 0.03, 0.685, 0.22);
$easeOutQuart:    cubic-bezier(0.165, 0.84, 0.44, 1);
$easeInOutQuart:  cubic-bezier(0.77, 0, 0.175, 1);
$easeInQuint:     cubic-bezier(0.755, 0.05, 0.855, 0.06);
$easeOutQuint:    cubic-bezier(0.23, 1, 0.32, 1);
$easeInOutQuint:  cubic-bezier(0.86, 0, 0.07, 1);
$easeInExpo:      cubic-bezier(0.95, 0.05, 0.795, 0.035);
$easeOutExpo:     cubic-bezier(0.19, 1, 0.22, 1);
$easeInOutExpo:   cubic-bezier(1, 0, 0, 1);
$easeInCirc:      cubic-bezier(0.6, 0.04, 0.98, 0.335);
$easeOutCirc:     cubic-bezier(0.075, 0.82, 0.165, 1);
$easeInOutCirc:   cubic-bezier(0.785, 0.135, 0.15, 0.86);
$easeInBack:      cubic-bezier(0.6, -0.28, 0.735, 0.045);
$easeOutBack:     cubic-bezier(0.175, 0.885, 0.32, 1.275);
$easeInOutBack:   cubic-bezier(0.68, -0.55, 0.265, 1.55);

@mixin mediaquery($device) {
  @if $device == pc {
    @media only screen and (min-width: $breakpoint+1) {
      @content; } }
  @else if $device == sp {
    @media only screen and (max-width: $breakpoint) {
      @content; } } }

@mixin clearfix {
  zoom: 1;

  &:after {
    clear: both;
    display: block;
    content: ""; } }


@mixin setFont($font-size: $font-size, $line-height: $line-height, $margin-bottom: 0, $margin-top: 0) {
  font-size: $font-size;
  line-height: $line-height;
  margin-bottom: floor($margin-bottom - ($font-size * $line-height - $font-size) / 2);
  margin-top: floor($margin-top - ($font-size * $line-height - $font-size) / 2); }

@mixin setMargin($font-size: $font-size, $line-height: $line-height, $margin-bottom: 0, $margin-top: 0) {
  margin-bottom: floor($margin-bottom - ($font-size * $line-height - $font-size) / 2);
  margin-top: floor($margin-top - ($font-size * $line-height - $font-size) / 2); }

@mixin setImg($width: auto, $height: auto) {
  width: $width;
  height: $height;
  img {
    width: 100%; } }

@mixin centeringPc {
  width: 1000px;
  margin: 0 auto;
  box-sizing: border-box; }

@mixin centeringSp {
  margin: 0 15px; }

@mixin ff_TPSky_EL {
  font-family: "TPSkyStdN-CLoEL", 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
 }  // font-weight: 100;

@mixin ff_TPSky_L {
  font-family: "TPSkyStdN-CLoL", 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
 }  // font-weight: 200;

@mixin ff_TPSky_R {
  font-family: "TPSkyStdN-CLoR", 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
 }  // font-weight: 400;

@mixin ff_TPSky_B {
  font-family: "TPSkyStdN-CLoB", 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
 }  // font-weight: 700;

@keyframes vertical {
  0% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

@keyframes movelogo {
  0% {
    background-position: 0px 0px; }
  100% {
    background-position: 0px -5859px; } }

@keyframes moveLoading {
  0% {
    background-position: 0px 0px; }
  100% {
    background-position: 0px -2100px; } }
