/*-----------------------------------------------------
add reset
-------------------------------------------------------*/

*{
  margin: 0;
  padding: 0;
}

h1,h2,h3,h4,h5,h6,caption,address,th{
  font-style:normal;
  font-weight:normal;
}

ul,ol,li,dl,dt,dd{
  list-style-type:none;
  list-style-position:outside;
}

table,th,td{
  vertical-align:top;
}
a{
  margin:0;
  padding:0;
  outline:none;
  border:0;
  font-size:100%;
  vertical-align:baseline;
  background:transparent;
}
img{
  border:0;
  vertical-align:top;
}
br{
  letter-spacing:0;
}
button,fieldset,form,input,label,legend,select,textarea{
  margin:0;
  padding:0;
  vertical-align:baseline;
  font-family:inherit;
  font-style:inherit;
  font-weight:inherit;
  font-size:100%;
  outline:none;
}
textarea{
  resize:vertical;
}
select{
  padding:2px;
  background:#FFF;
}
input,select{
  outline:none;
  vertical-align:middle;
}

input, button, textarea, select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

/*-----------------------------------------------------
CLEARFIX
-------------------------------------------------------*/

/* clearfix (Firefox)
------------------------------- */
.clearfix:after{
  content:".";
  display:block;
  height:0px;
  clear:both;
  visibility:hidden;
}

/* WinIE7
------------------------------- */
.clearfix{
  display:inline-block;
}

/* WinIE6
------------------------------- */
/* Hides from IE-mac \*/
* html .clearfix{
  height:1px;
}
.clearfix{
  display:block;
}